import React, { useState } from "react";
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

const paymentsData = [
  { month: "Feb 2025", details: "Expense details here..." },
  { month: "Jan 2025", details: "Expense details here..." },
  { month: "Dec 2024", details: "Expense details here..." }
];

const Payments = () => {
  const [expanded, setExpanded] = useState(false);

  return (
    <div>
      {paymentsData.map((item, index) => (
        <Accordion key={index} expanded={expanded === index} onChange={() => setExpanded(expanded === index ? false : index)}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography variant="h6">{item.month}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>{item.details}</Typography>
          </AccordionDetails>
        </Accordion>
      ))}
    </div>
  );
};

export default Payments;